* {
  box-sizing: border-box;

}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
a, li, ul {
  margin: 0;
  padding: 0;
  border: none;
  list-style: none;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.42);
}
textarea {

}
body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
